<template>
  <div class="user-exhibition">
    <TopBar :title="title"/>
    <div class="icons">
      <div class="comment" @click="showCommentPopup">
        <Icon name="comment"/>
        <span>{{ commentTotal }}</span>
      </div>
      <div class="like">
        <Icon class="like" name="like"/>
        <span>413</span>
      </div>
    </div>
    <div class="items">

      <router-link to="/detail" class="item" v-for="work in workList" :key="work.id">
        <img class="light" src="../../assets/images/exhibition/灯光.png" alt="灯光">
        <div class="item-container">
          <img :src="work.img" :alt="work.name">
          <Icon v-if="work.mp4" name="play"/>
        </div>
        <div class="table">
          <img src="../../assets/images/exhibition/台面.png" alt="台面">
          <span>{{ work.author }}</span>
        </div>
        <span class="theme">{{ work.theme }}</span>
      </router-link>

    </div>
    <van-popup position="bottom" round
               :closeable="true"
               v-model="isShowComment">
      <div class="comment-content">
        <h3>{{ commentTotal }}条评论</h3>
        <div class="comment-list">

          <div class="comment-item" v-for="comment in commentList" :key="comment.id">
            <div class="avatar">
              <img :src="comment.avatar" alt="头像">
            </div>
            <div class="comment-info">
              <div class="name">{{ comment.user }}</div>
              <div class="content">{{ comment.content }}</div>
              <div>
                <div class="time">{{ comment.time }}</div>
                <div class="comment-like">
                  <Icon name="like"/>
                  <span>{{ comment.like }}</span>
                </div>
              </div>
            </div>

          </div>
        </div>
        <div class="comment-input">
          <div>
            <input v-model="userComment"
                   @keyup.enter="addComment"
                   placeholder="请输入评论内容"/>
            <span @click="addComment">发送</span>
          </div>
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
import TopBar from '@/components/TopBar'
import work1 from '@/assets/images/exhibition/exhibit1.png'
import work2 from '@/assets/images/exhibition/藏品1.png'
import work3 from '@/assets/images/exhibition/音乐藏品.png'
import work4 from '@/assets/images/exhibition/精彩片段1.jpg'

import avatar1 from '@/assets/images/exhibition/弹幕头像.jpg'
import avatar2 from '@/assets/images/exhibition/弹幕头像2.jpg'
import avatar3 from '@/assets/images/exhibition/弹幕头像3.jpg'

export default {
  name: 'UserExhibitionView',
  components: {TopBar},
  data() {
    return {
      title: 'xxx用户的展览馆',
      commentDialogVisible: false,
      workList: [
        {id: 1, author: 'LUNA', img: work1, theme: '展览主题文字内容'},
        {id: 2, author: 'xxx', img: work2, theme: '展览主题文字内容'},
        {id: 3, author: 'xxx', img: work3, theme: '展览主题文字内容'},
        {id: 4, author: 'xxx', mp4: true, img: work4, theme: '展览主题文字内容'},
      ],
      isShowComment: false,
      commentList: [
        {
          id: 1,
          user: 'xxx',
          avatar: avatar1,
          content: '哇，好喜欢这个展馆，展品都很棒！',
          time: '2019-01-01',
          like: 1239
        },
        {
          id: 2,
          user: 'xxx',
          avatar: avatar2,
          content: '想要同款LUNA藏品~',
          time: '2019-01-01',
          like: 345
        },
        {
          id: 3,
          user: 'xxx',
          avatar: avatar3,
          content: '评论内容',
          time: '2019-01-01',
          like: 1222
        },
        {
          id: 4,
          user: 'xxx',
          avatar: avatar1,
          content: '评论内容',
          time: '2019-01-01',
          like: 234
        },
        {
          id: 5,
          user: 'xxx',
          avatar: avatar2,
          content: '评论内容',
          time: '2019-01-01',
          like: 234
        },
        {
          id: 6,
          user: 'xxx',
          avatar: avatar3,
          content: '评论内容',
          time: '2019-01-01',
          like: 23
        },
      ],
      userComment: '',
    }
  },
  computed: {
    commentTotal() {
      return this.commentList.length
    }
  },
  methods: {
    showCommentPopup() {
      this.isShowComment = true
    },
    addComment() {
      if (!this.userComment) return
      this.commentList.unshift({
        id: this.commentList.length + 1,
        user: 'hello',
        avatar: avatar1,
        content: this.userComment,
        time: new Date().toLocaleString(),
        like: 0,
      })
      this.userComment = ''
    }
  },
}
</script>

